import React, { useState } from "react";

const ContactForm = () => {
  const [form, setForm] = useState({ name: "", email: "", subject: "", message: "" });
  const [error, setError] = useState("");
  const [sent, setSent] = useState(false);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
    setError("");
  };

  const validateEmail = (email) => {
    return /^\S+@\S+\.\S+$/.test(email);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!form.name.trim() || !form.email.trim() || !form.message.trim()) {
      setError("Please fill your name, email and a short message.");
      return;
    }

    if (!validateEmail(form.email)) {
      setError("Please provide a valid email address.");
      return;
    }

    const subject = form.subject.trim() || "Portfolio Contact";
    const body = `Name: ${form.name}\nEmail: ${form.email}\n\n${form.message}`;
    const mailto = `mailto:meetadlakha1@gmail.com?subject=${encodeURIComponent(
      subject
    )}&body=${encodeURIComponent(body)}`;

    // Attempt to open the user's email client with a prefilled message.
    try {
      window.location.href = mailto;
      setSent(true);
    } catch (err) {
      setError("Unable to open your mail client. You can email directly to meetadlakha1@gmail.com");
    }
  };

  return (
    <div className="relative bg-white dark:bg-gray-900 rounded-3xl p-8 sm:p-12 shadow-2xl border border-gray-100 dark:border-gray-800">
      <h3 className="text-4xl font-black text-gray-800 dark:text-gray-100 mb-4">Contact Me</h3>
      <p className="text-gray-600 dark:text-gray-300 mb-6">Prefer to email directly? This form will open your email client and prefill a message to <span className="font-bold">meetadlakha1@gmail.com</span>.</p>

      {sent ? (
        <div className="p-6 bg-green-50 dark:bg-green-900/30 rounded-lg text-green-800 dark:text-green-200 font-bold">
          Your mail client should open shortly. If it didn't, please email meetadlakha1@gmail.com directly.
        </div>
      ) : (
        <form onSubmit={handleSubmit} className="grid grid-cols-1 gap-4">
          <div>
            <label className="block text-sm font-bold text-gray-700 dark:text-gray-300 mb-1">Name</label>
            <input name="name" value={form.name} onChange={handleChange} className="w-full rounded-lg px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 text-gray-900 dark:text-gray-100" placeholder="Your name" />
          </div>

          <div>
            <label className="block text-sm font-bold text-gray-700 dark:text-gray-300 mb-1">Email</label>
            <input name="email" value={form.email} onChange={handleChange} className="w-full rounded-lg px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 text-gray-900 dark:text-gray-100" placeholder="you@example.com" />
          </div>

          <div>
            <label className="block text-sm font-bold text-gray-700 dark:text-gray-300 mb-1">Subject (optional)</label>
            <input name="subject" value={form.subject} onChange={handleChange} className="w-full rounded-lg px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 text-gray-900 dark:text-gray-100" placeholder="What's this about?" />
          </div>

          <div>
            <label className="block text-sm font-bold text-gray-700 dark:text-gray-300 mb-1">Message</label>
            <textarea name="message" value={form.message} onChange={handleChange} rows={6} className="w-full rounded-lg px-4 py-3 bg-gray-50 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 text-gray-900 dark:text-gray-100" placeholder="Write a short message..." />
          </div>

          {error && <div className="text-sm text-red-600 dark:text-red-400 font-medium">{error}</div>}

          <div className="flex items-center gap-4 mt-2">
            <button type="submit" className="px-6 py-3 bg-gradient-to-r from-blue-600 via-purple-600 to-pink-600 text-white rounded-2xl font-black shadow-lg hover:opacity-95">Send Email</button>
            <a href="mailto:meetadlakha1@gmail.com" className="text-sm text-gray-700 dark:text-gray-300 underline">Or email directly</a>
          </div>
        </form>
      )}
    </div>
  );
};

export default ContactForm;
